import React, { useState } from 'react'
import Mode from '../Mode/Mode';
import './Home.css'
const Home = () => {

    const [amount, setAmount] = useState('');
    const [alert, setAlert] = useState('');
    const[alertClass,setAlertClass]= useState('')
    const [deductedAmount, setDeductedAmount] = useState('')

    let changeHandler = (e) => {
        setAmount(e.target.value)
        let newAmount = e.target.value;
        console.log("after", newAmount)
        setTimeout(() => {
            if (newAmount >= 10000 && newAmount < 100000) {
                setAlert("Amount is between Ten Thousand and 1 Lakh")
                setAlertClass("alert-text-orange")
            }
            else if (newAmount >= 100000 && newAmount < 1000000) {
                setAlert("Amount is between 1 Lakh and Ten Lakhs ")
                setAlertClass("alert-text-green")

            } else if (newAmount >= 1000000) {
                setAlert("Amount is greater than or equal to  10 Lakhs")
                setAlertClass("alert-text-red")

            } else {
                setAlert("")
            }
        }, 1000)
    }

    let deductHandler = (e) => {
        setDeductedAmount(e.target.value)
    }
    return (
        <div>
            {/* <Mode /> */}
            <div>
                <p className="text">Enter Amount here</p>
                <input type="number" onChange={changeHandler} value={amount} />
                <span className={alertClass}>
                    {alert}
                </span>
            </div>
            <div>
                <p className="text">Enter Amount to be deducted here</p>
                <input type="number" onChange={deductHandler} value={deductedAmount} />
            </div>
            <div className="details">
                <div>
                    <span>Actual Amount</span>
                    <span>= {amount}</span>
                </div>
                <div>
                    <span>Deducted Amount</span>
                    <span>={deductedAmount}</span>
                </div>
                {
                    deductedAmount > 0 ?
                        <div className="final-amount">
                            <span>Final Amount</span>
                            <span>={amount - deductedAmount}</span>
                        </div> : null
                }
            </div>

        </div>
    )
}

export default Home
